
import { Button } from 'antd'
import React, { useContext } from 'react'
import { ContextValue } from './index'
function Item() {
  const contextObj = useContext(ContextValue)
  return (
    <div>
      <Button onClick={contextObj.changeCount}>最底层改变数据</Button>
      这个数据是最高层传下来的：{contextObj.text}
    </div>
  )
}

export default Item
